import style from "./reset-password.module.css";
import {
  PasswordInput,
  Input,
  Button,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Link, Navigate, useNavigate } from "react-router-dom";
import { resetUserPassword } from "../../utils/workWithApi";
import { FormEventHandler } from "react";
import { useForm } from "../../hooks/useForm";

export default function ResetPassword() {
  const navigate = useNavigate();

  const { values, handleChange } = useForm({ password: "", token: "" });

  const handleSubmit: FormEventHandler<HTMLFormElement> = e => {
    e.preventDefault();
    resetUserPassword(values.password, values.token);
    navigate("/");
  };

  return localStorage.getItem("emailSent") === "true" ? (
    <section className={style.main}>
      <form className={style.loginForm} onSubmit={handleSubmit}>
        <h2 className="text text_type_main-medium"> </h2>
        <PasswordInput
          onChange={e => handleChange(e)}
          value={values.password}
          name={"password"}
          placeholder="  "
          extraClass="mt-6"
        />
        <Input
          type={"text"}
          placeholder={"   "}
          onChange={e => handleChange(e)}
          value={values.token}
          name={"token"}
          error={false}
          size={"default"}
          extraClass="mt-6"
        />
        <div className={`${style.button} mt-6`}>
          <Button htmlType="submit" type="primary" size="medium">
            
          </Button>
        </div>
        <p className="text text_type_main-default text_color_inactive mt-20">
           ?
          <Link
            to="/login"
            className={`${style.paragraphLink} text text_type_main-default`}
          >
            
          </Link>
        </p>
      </form>
    </section>
  ) : (
    <Navigate to="/" />
  );
}
