import { useAppSelector } from "../..";
import { Ingredient } from "../../utils/types";
import style from "./ingredient-details.module.css";
import { useLocation, useParams } from "react-router-dom";

export default function IngredientDetails() {
  const id = useParams();

  const location = useLocation();

  const info: Ingredient | undefined = useAppSelector(
    state => state.ingredients.ingredients,
  ).find((item: Ingredient) => item._id === id.ingredientId);

  const background = location.state && location.state.background;

  return info ? (
    <div className={background !== null ? style.main : style.mainTop}>
      <div
        className={`${
          background !== null ? style.head : style.headCenter
        } mt-10`}
      >
        <p className={`${style.title} text text_type_main-large`}>
           
        </p>
      </div>
      <img
        className={style.img}
        src={info.image_large}
        alt="  "
      />
      <p className={`${style.info} text text_type_main-medium mt-4`}>
        {info.name}
      </p>
      <ul className={`${style.details} mt-8 mb-15`}>
        <li className={style.callories}>
          <p className="text text_type_main-default">,</p>
          <p className="text text_type_digits-default mt-2">{info.calories}</p>
        </li>
        <li className={style.callories}>
          <p className="text text_type_main-default">, </p>
          <p className="text text_type_digits-default mt-2">{info.proteins}</p>
        </li>
        <li className={style.callories}>
          <p className="text text_type_main-default">, </p>
          <p className="text text_type_digits-default mt-2">{info.fat}</p>
        </li>
        <li className={style.callories}>
          <p className="text text_type_main-default">, </p>
          <p className="text text_type_digits-default mt-2">
            {info.carbohydrates}
          </p>
        </li>
      </ul>
    </div>
  ) : (
    <div> </div>
  );
}
